<template>
  <div>
    <van-nav-bar title="购物车" />
    <van-card
      v-for="item in cartList"
      :key="item.id"
      :price="item.price"
      :title="item.goodsname"
      :thumb="$preImg+item.img"
    >
      <template #footer>
        <van-stepper v-model="item.num" theme="round" button-size="22" disable-input />
    <van-button class="del" type="primary" size="mini" @click="del(item.id)">删除</van-button>
      </template>
    </van-card>
    <van-submit-bar class="sub" :price="sum" button-text="提交订单"  />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { delCart } from '../utils/req';
export default {
  computed: {
    ...mapGetters({
      cartList: "cartList",
      user: "user",
      sum: "sum",
    }),
  },
  methods: {
    ...mapActions({
      reqCart: "cartListActions",
    }),
    del(id){
      delCart({id}).then(res=>{
        this.reqCart(this.user.uid)
        this.$toast.success('删除成功')
      })
    }
  },
  mounted() {
    this.reqCart(this.user.uid);
  },
};
</script>

<style scoped>
.sub{
  margin-bottom: 50px;
}
</style>


